
.sellerlist {
  width: 100%;
  height: 96px;
  display: block;
  box-sizing: border-box;
  padding: 20px 20px;
  position: relative;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.sellerlist a {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  padding-left: 86px;
}
.sellerlist a .avatar {
  width: 76px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.sellerlist a .sellerlist-content .sellerlist-name .name {
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  color: #333;
}
.sellerlist a .sellerlist-content .sale .star {
  display: inline-block;
  margin-right: 8px;
  vertical-align: top;
}
.sellerlist a .sellerlist-content .sale .text {
  font-size: 10px;
  line-height: 16px;
  color: #7e8c8d;
}
.sellerlist a .sellerlist-content .sale .time {
  display: inline-block;
  position: absolute;
  font-size: 10px;
  line-height: 16px;
  top: 45%;
  right: 25px;
  color: #7e8c8d;
}
.sellerlist a .sellerlist-content .dilivery .text {
  font-size: 10px;
  line-height: 16px;
  border-right: 1px solid rgba(7,17,27,0.1);
  padding-right: 7px;
  color: #7e8c8d;
}
.sellerlist a .sellerlist-content .dilivery .text:last-child {
  border-right: 0;
  padding-left: 7px;
}


