
.collect .header {
  box-sizing: border-box;
  padding: 34px 15px 0 15px;
}
.collect .header .warpper {
  width: 100%;
  font-size: 14px;
  line-height: 14px;
  color: #fff;
}
.collect .header .warpper .title {
  position: absolute;
  top: 34px;
  left: 45px;
  right: 45px;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  color: #fff;
}
.collect .header .warpper span {
  float: right;
}
.collect .header .warpper a {
  float: left;
  color: #fff;
}
.collect .collect-wrapper {
  position: absolute;
  top: 75px;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}


