
.home {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.home .swipe-warrper {
  font-size: 0;
}
.home .swipe-warrper .tool {
  position: absolute;
  top: 32px;
  left: 90px;
  right: 10px;
  height: 24px;
  font-weight: 700;
}
.home .swipe-warrper .tool .address {
  position: absolute;
  left: 0;
  right: 82px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  border-radius: 25px;
  background-color: rgba(7,17,27,0.3);
  line-height: 24px;
  color: #fff;
  padding: 0 10px 0 16px;
}
.home .swipe-warrper .tool .address .icon-drop_down {
  width: 24px;
  text-align: center;
  display: block;
  line-height: 24px;
  position: absolute;
  top: 0;
  right: 0;
  font-size: 10px;
  color: #fff;
}
.home .swipe-warrper .tool .home-search {
  width: 74px;
  height: 24px;
  border-radius: 25px;
  background-color: rgba(7,17,27,0.3);
  line-height: 24px;
  color: #fff;
  font-size: 14px;
  text-align: center;
  position: absolute;
  top: 0;
  right: 0;
}
.home .species {
  display: -ms-flexbox;
  display: flex;
  box-sizing: border-box;
  width: 100%;
  padding: 22px 14px;
}
.home .species .species-warrper {
  -ms-flex: 1;
      flex: 1;
  display: inline-block;
  text-align: center;
}
.home .species .species-warrper i {
  font-size: 44px;
  line-height: 44px;
  color: #ff5656;
}
.home .species .species-warrper p {
  padding-top: 10px;
  font-size: 12px;
  line-height: 12px;
}
.home .seller-warrper .title {
  box-sizing: border-box;
  width: 100%;
  height: 42px;
  padding-left: 10px;
  font-size: 13px;
  line-height: 42px;
  color: #a9a9a9;
  position: relative;
}
.home .seller-warrper .title:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.home .seller-warrper .seller {
  padding-bottom: 50px;
}
.home .seller-warrper .data-loader-home {
  width: 100%;
  height: 400px;
  background: url("../../common/loading.gif") center no-repeat;
}


