
.selectaddress {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
}
.selectaddress .header {
  box-sizing: border-box;
  padding: 34px 15px 0 15px;
}
.selectaddress .header .wrapper {
  width: 100%;
  font-size: 14px;
  line-height: 14px;
  color: #fff;
}
.selectaddress .header .wrapper .title {
  display: inline-block;
  font-weight: 700;
  text-align: center;
}
.selectaddress .header .wrapper span {
  float: right;
}
.selectaddress .header .wrapper i {
  float: left;
  color: #fff;
}
.selectaddress .address-list {
  position: relative;
  width: 100%;
  height: 92px;
  box-sizing: border-box;
  padding: 33px 15px 19px 15px;
  position: relative;
}
.selectaddress .address-list:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.2);
  content: ' ';
}
.selectaddress .address-list .addressdes {
  box-sizing: border-box;
  padding-left: 24px;
}
.selectaddress .address-list .addressdes .icon-check_circle {
  position: absolute;
  left: 14px;
  font-size: 16px;
  color: #ccc;
}
.selectaddress .address-list .addressdes .icon-check_circle.checked {
  color: #ff2d4b;
}
.selectaddress .address-list .addressdes .addressdes-wrapper .des {
  width: 80%;
  font-size: 16px;
  font-weight: 700;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #333;
}
.selectaddress .address-list .addressdes .addressdes-wrapper .contact {
  padding-top: 10px;
  font-size: 0;
}
.selectaddress .address-list .addressdes .addressdes-wrapper .contact span {
  font-size: 14px;
  color: #919191;
}
.selectaddress .address-list .addressdes .addressdes-wrapper .contact .name {
  padding-right: 5px;
}
.selectaddress .address-list .addressdes .addressdes-wrapper .contact .sex {
  padding-right: 12px;
}
.selectaddress .address-list .addressdes .addressdes-wrapper i {
  position: absolute;
  right: 21px;
  top: 40px;
  font-size: 30px;
  color: #bababa;
}


