
.editorAdderss {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
}
.editorAdderss .editorAddress-header {
  width: 100%;
  height: 75px;
  box-sizing: border-box;
  padding: 34px 15px 0 15px;
  background: #ff2d4b;
}
.editorAdderss .editorAddress-header .header-wrapper {
  position: relative;
  width: 100%;
  font-size: 14px;
  line-height: 14px;
  color: #fff;
}
.editorAdderss .editorAddress-header .header-wrapper .title {
  position: absolute;
  left: 45px;
  right: 45px;
  display: inline-block;
  font-weight: 700;
  text-align: center;
}
.editorAdderss .editorAddress-header .header-wrapper i {
  float: left;
}
.editorAdderss .editorAddress-content {
  padding: 16px 15px;
}
.editorAdderss .editorAddress-content .content-wrapper .editorAddress-group {
  height: 48px;
  line-height: 48px;
  padding-left: 78px;
  position: relative;
  font-size: 14px;
  border-bottom: 1px solid #ddd;
}
.editorAdderss .editorAddress-content .content-wrapper .editorAddress-group .editorAddress-label {
  position: absolute;
  top: 0;
  left: 0;
  font-weight: 700;
  line-height: 48px;
  font-size: 14px;
}
.editorAdderss .editorAddress-content .content-wrapper .editorAddress-group .editorAddress-input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
}
.editorAdderss .editorAddress-content .content-wrapper .editorAddress-group .editorAddress-checkbox {
  display: inline-block;
}
.editorAdderss .editorAddress-content .content-wrapper .editorAddress-group .editorAddress-checkbox i.icon-check_circle {
  color: #d2d2d7;
}
.editorAdderss .editorAddress-content .content-wrapper .editorAddress-group .editorAddress-checkbox i.icon-check_circle.checked {
  color: #ff2d4b;
}
.editorAdderss .editorAddress-content .save {
  margin: 16px 0;
  width: 100%;
  height: 38px;
  text-align: center;
  color: #fff;
  background: #ff2b4d;
  border: 0;
  border-radius: 5px;
  outline: none;
}
.editorAdderss .editorAddress-content .delete {
  width: 100%;
  height: 38px;
  text-align: center;
  color: #ff2b4d;
  background: #fff;
  border: 1px solid #ff2b4d;
  border-radius: 5px;
  outline: none;
}
.editorAdderss .editorAddress-content .change {
  margin: 16px 0;
  width: 100%;
  height: 38px;
  text-align: center;
  color: #fff;
  background: #ff2b4d;
  border: 0;
  border-radius: 5px;
  outline: none;
}


