
.tab {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  line-height: 50px;
  background-color: #f9f9f9;
  border-top: 1px solid rgba(7,17,27,0.1);
}
.tab .tab-item {
  -ms-flex: 1;
      flex: 1;
  height: 100%;
  box-sizing: border-box;
  text-align: center;
  padding: 6px 0;
}
.tab .tab-item .tab-wrap {
  width: 100%;
  height: 100%;
  position: relative;
}
.tab .tab-item .tab-wrap i {
  width: 100%;
  display: block;
  line-height: 24px;
  font-size: 24px;
  color: #999;
  position: absolute;
  left: 0;
  top: 0;
}
.tab .tab-item .tab-wrap a {
  width: 100%;
  display: block;
  font-size: 14px;
  line-height: 14px;
  text-decoration: none;
  color: #999;
  position: absolute;
  left: 0;
  bottom: 0;
}
.tab .tab-item .tab-wrap a.router-link-active {
  color: #ff2d4b;
}
.tab .tab-item .tab-wrap a.router-link-active+i {
  color: #ff2d4b;
}


