
.search {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  font-size: 0;
  background: #eee;
}
.search .search-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 64px;
  box-sizing: border-box;
  padding-top: 28px;
  background: #ff2d4b;
}
.search .search-header .header-wrapper .icon-arrow_lift {
  padding-left: 10px;
  font-size: 24px;
  color: #fff;
}
.search .search-header .header-wrapper .search-input {
  position: absolute;
  left: 45px;
  right: 8px;
  display: inline-block;
  box-sizing: border-box;
  padding: 0 10px;
  border-radius: 12px;
  vertical-align: top;
  font-size: 14px;
  line-height: 26px;
  background: #fff;
}
.search .search-header .header-wrapper .search-input input {
  width: 80%;
  outline: none;
  padding: 0 5px;
}
.search .search-content {
  width: 100%;
  position: absolute;
  left: 0;
  top: 64px;
  bottom: 0;
}
.search .search-content .hot-search {
  font-size: 14px;
}
.search .search-content .hot-search .title {
  padding-left: 15px;
  font-weight: 600;
  line-height: 38px;
  color: #666;
}
.search .search-content .hot-search .hotsearch-list {
  width: 100%;
  background: #fff;
  overflow: hidden;
  padding: 7.5px;
}
.search .search-content .hot-search .hotsearch-list .hotsearch-name {
  float: left;
  padding: 5px 10px;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 7.5px;
}
.search .search-content .searchResults {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
  overflow: hidden;
}
.search .search-content .searchResults .search-results .searchlist {
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.search .search-content .searchResults .search-results .searchlist .seller {
  width: 100%;
  height: 96px;
  display: block;
  box-sizing: border-box;
  padding: 20px 20px;
  position: relative;
}
.search .search-content .searchResults .search-results .searchlist .seller .avatar {
  display: inline-block;
  width: 76px;
  height: 100%;
}
.search .search-content .searchResults .search-results .searchlist .seller .sellerlist-content {
  display: inline-block;
  vertical-align: top;
  margin: 0px 0 0 10px;
}
.search .search-content .searchResults .search-results .searchlist .seller .sellerlist-content .sellerlist-name .name {
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  color: #333;
}
.search .search-content .searchResults .search-results .searchlist .seller .sellerlist-content .sale .star {
  display: inline-block;
  margin-right: 8px;
  vertical-align: top;
}
.search .search-content .searchResults .search-results .searchlist .seller .sellerlist-content .sale .text {
  font-size: 10px;
  line-height: 16px;
  color: #7e8c8d;
}
.search .search-content .searchResults .search-results .searchlist .seller .sellerlist-content .sale .time {
  display: inline-block;
  position: absolute;
  font-size: 10px;
  line-height: 16px;
  top: 45%;
  right: 25px;
  color: #7e8c8d;
}
.search .search-content .searchResults .search-results .searchlist .seller .sellerlist-content .dilivery .text {
  font-size: 10px;
  line-height: 16px;
  border-right: 1px solid rgba(7,17,27,0.1);
  padding-right: 7px;
  color: #7e8c8d;
}
.search .search-content .searchResults .search-results .searchlist .seller .sellerlist-content .dilivery .text:last-child {
  border-right: 0;
  padding-left: 7px;
}
.search .search-content .searchResults .search-results .searchlist .search-foods {
  overflow: hidden;
  padding: 0 16px 18px 16px;
}
.search .search-content .searchResults .search-results .searchlist .search-foods .search-food {
  float: left;
  width: 30%;
  border: 1px solid #eee;
  margin: 0 1%;
  border-radius: 2px;
}
.search .search-content .searchResults .search-results .searchlist .search-foods .search-food .foodname {
  font-size: 12px;
  text-align: center;
  line-height: 30px;
}


