
.address .header {
  box-sizing: border-box;
  padding: 34px 15px 0 15px;
}
.address .header .wrapper {
  width: 100%;
  font-size: 14px;
  line-height: 14px;
  color: #fff;
}
.address .header .wrapper .title {
  display: inline-block;
  font-weight: 700;
  text-align: center;
}
.address .header .wrapper span {
  float: right;
}
.address .header .wrapper a {
  float: left;
  color: #fff;
}
.address .address-list {
  position: relative;
  width: 100%;
  height: 92px;
  box-sizing: border-box;
  padding: 33px 15px 19px 15px;
  position: relative;
}
.address .address-list:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.2);
  content: ' ';
}
.address .address-list .des {
  width: 80%;
  font-size: 16px;
  font-weight: 700;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #333;
}
.address .address-list .contact {
  padding-top: 10px;
  font-size: 0;
}
.address .address-list .contact span {
  font-size: 14px;
  color: #919191;
}
.address .address-list .contact .name {
  padding-right: 5px;
}
.address .address-list .contact .sex {
  padding-right: 12px;
}
.address .address-list i {
  position: absolute;
  right: 21px;
  top: 40px;
  font-size: 30px;
  color: #bababa;
}


