
.register {
  position: fixed;
  top: 0;
  left: 0;
  background: #fafafa;
  width: 100%;
  height: 100%;
  font-size: 0;
}
.register .registerHeader {
  box-sizing: border-box;
  padding: 34px 15px 0 15px;
  width: 100%;
  height: 75px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  background: #fff;
}
.register .registerHeader .warpper {
  width: 100%;
  font-size: 14px;
  line-height: 14px;
  color: #f00;
}
.register .registerHeader .warpper .title {
  position: absolute;
  top: 34px;
  left: 45px;
  right: 45px;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  color: #4d4d4d;
}
.register .registerHeader .warpper span {
  float: right;
}
.register .registerContent {
  width: 100%;
  box-sizing: border-box;
  padding: 150px 16px;
}
.register .registerContent .registerWarpper {
  border-radius: 12px;
  border: 1px solid rgba(7,17,27,0.1);
}
.register .registerContent .registerWarpper input {
  display: block;
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  height: 50px;
  font-size: 12px;
  line-height: 12px;
  border-radius: 12px;
  outline: none;
}
.register .registerContent .registerWarpper i {
  width: 100%;
  display: block;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.register .registerContent .registerButton {
  margin-top: 24px;
  width: 100%;
  height: 50px;
  border-radius: 5px;
  border: 0;
  outline: none;
  background: #367cff;
  color: #fff;
}


