
.cartontrol {
  font-size: 0;
}
.cartontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
}
.cartontrol .cart-decrease.move-enter-active,
.cartontrol .cart-decrease.move-leave-active {
  transition: all 0.4s linear;
}
.cartontrol .cart-decrease .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  transition: all 0.4s linear;
  transform: rotate(0);
}
.cartontrol .cart-decrease.move-enter,
.cartontrol .cart-decrease.move-leave-active {
  opacity: 0;
  transform: translate3d(24px, 0, 0);
}
.cartontrol .cart-decrease.move-enter .inner,
.cartontrol .cart-decrease.move-leave-active .inner {
  transform: rotate(180deg);
}
.cartontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartontrol .cart-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}


