
.variety {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
.variety .varietyHeader {
  box-sizing: border-box;
  padding: 20px 15px 0 15px;
  width: 100%;
  height: 50px;
  background-color: #ff2d4b;
  color: #fff;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.variety .varietyHeader .title {
  position: absolute;
  top: 20px;
  left: 45px;
  right: 45px;
  display: inline-block;
  font-weight: 700;
  text-align: center;
}
.variety .varietyHeader a {
  color: #fff;
}
.variety .varietNat {
  width: 100%;
  height: 26px;
  box-sizing: border-box;
}
.variety .varietNat .varietNat-warpper {
  width: 100%;
  white-space: nowrap;
}
.variety .varietNat .varietNat-warpper .nat-list {
  position: relative;
  font-size: 0;
}
.variety .varietNat .varietNat-warpper .nat-list .nat-item {
  display: inline-block;
  width: 76px;
  line-height: 26px;
  font-size: 12px;
  text-align: center;
}
.variety .varietNat .varietNat-warpper .nat-list .nat-item.nat-item-active {
  color: #ff2d4b;
}
.variety .varietNat .varietNat-warpper .nat-list .nat-item span {
  transition: all 0.3s;
}
.variety .varietNat .varietNat-warpper .nat-list .variet-line {
  transition: all 0.3s;
  width: 76px;
  position: absolute;
  bottom: -3px;
  left: 0;
  border-bottom: 2px solid #ff2d4b;
}
.variety .data-loader-variety {
  width: 100%;
  height: 400px;
  background: url("../../../common/loading.gif") center no-repeat;
}


