
.swiper {
  width: 100%;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
}
.swiper .swiper-list {
  position: relative;
  top: 0;
}
.swiper.swiper-transition {
  transition: all 0.3s linear;
}
.swiper .swiper-control {
  overflow: hidden;
  position: absolute;
  padding: 5px 0;
  right: 10px;
  bottom: 10px;
  z-index: 10;
}
.swiper .swiper-control .swiper-control-item {
  width: 8px;
  height: 8px;
  border: #2f2f2f;
  border-radius: 4px;
  opacity: 0.5;
  background: #fff;
  float: left;
  margin: 0 5px;
}
.swiper .swiper-control.swiper-transition {
  transition: all 0.3s linear;
}
.swiper .swiper-control:hover {
  opacity: 1;
  cursor: pointer;
}
.swiper .swiper-control.swiper-control-active {
  transform: scale(1.3, 1.3);
  opacity: 1;
}


